<script>
import { mapState } from 'vuex'
export default {
  name: 'nava',
  data () {
    return {
      timer: '',
      iframe: ''
    }
  },
  computed: {
    ...mapState([
      'canvasArr',
      'nowCanvas'
    ])
  },
  methods: {
    openImg: function () {
      var inputObj = document.createElement('input')
      inputObj.addEventListener('change', readFile, false)
      inputObj.type = 'file'
      inputObj.accept = 'image/*'
      inputObj.click()
      var a = this
      function readFile () {
        var file = this.files[0]// input
        var reader = new FileReader()
        reader.readAsDataURL(file)// base64
        reader.onload = function (e) {
          var img = new Image()
          img.onload = function () {
            var canvas = document.createElement('canvas')
            canvas.setAttribute('width', img.width)
            canvas.setAttribute('height', img.height)
            var context = canvas.getContext('2d')
            context.drawImage(img, 0, 0)
            var data = context.getImageData(0, 0, img.width, img.height)
            var obj = {
              id: a.canvasArr.length,
              name: file.name,
              width: img.width,
              height: img.height,
              context: '',
              canvas: '',
              imgData: data
            }
            a.$store.commit('addCanvasArr', obj)
          }
          img.src = this.result
        }
      }
    },
    selectGrayscale: function (string) {
      this.$store.commit('changeSelectGrayscale', string)
    },
    showTools: function () {
      this.$store.commit('changeShowTools', true)
    },
    showRecord: function () {
      this.$store.commit('changeShowRecord', true)
    },
    // 
    Saveas: function () {
      var name = this.canvasArr[this.nowCanvas].name
      var canvas = this.canvasArr[this.nowCanvas].canvas.toDataURL('image/png')
      this.downloadFile(name, canvas)
    },
    downloadFile: function (fileName, content) {
      var aLink = document.createElement('a')
      aLink.download = fileName
      aLink.href = content
      aLink.dataset.downloadurl = ['image/png', aLink.download, aLink.href].join(':')
      document.body.appendChild(aLink)
      aLink.click()
      document.body.removeChild(aLink)
    }
  }
}
</script>

<style lang="scss">
  .nav {
    height: 27px;
    border: 1px solid #95B8E7;
    background-color: #EDF5FA;
    display: flex;
    font-size: 12px;
    &>div {
      position: relative;
      margin-right: 2px;
      cursor: pointer;
      &>span {
        display: inline-block;
        width: 50px;
        height: 25px;
        line-height: 25px;
        text-align: center;
        border: 1px solid transparent;
        &:hover {
