          border-radius: 5px;
        }
      }
      .navHide {
        position: absolute;
        z-index: 1000;
        font-size: 12px;
        display: none;
        padding: 2px;
        top: 27px;
        width: 150px;
        border: 1px solid #ddd;
        background-color: #fafafa;
        box-shadow: 2px 2px 3px #cccccc;
        .navLine {
          position: absolute;
          left: 24px;
          top: -2px;
          border-right: 1px solid #ccc;
          height: 100%;
        }
        .navSep {
          width: 129px;
          border-top: 1px solid #ccc;
          border-bottom: 1px solid #fff;
          margin: 3px 0 3px 23px;
        }
        .navBox {
          height: 20px;
          border: 1px solid transparent;
          position: relative;
          img {
            margin: 2px 2px;
          }
          span:nth-of-type(1) {
            position: absolute;
            vertical-align: top;
            line-height: 20px;
            height: 20px;
            display: inline-block;
            left: 26px;
          }
          span:nth-of-type(2) {
            float: right;
            padding-right: 5px;
          }
          .navBoxHide {
            margin-top: 2px;
            background: url('../../src/assets/default/menu_arrows.png') no-repeat -32px center;
          }
          &>div {
            position: absolute;
            left: 150px;
            top: -4px;
            display: none;
          }
          &:hover {
            border-color: #95BCFC;
            border-radius: 5px;
            background-color: #D1DEF2;
            &>div {
              display: block;
            }
          }
        }
      }
      &:hover {
        .navHide {
          display: block;
        }
      }
    }
  }
</style>



